import React from "react";
import Nav from "./Nav";
import { Box, Heading, Text } from "@chakra-ui/react"; 
import { useState } from "react";
import { useEffect } from "react";

const UserProfile = () => {
    const [ user, setUser ] = useState('');

    useEffect( () => {
        const fetchData = async () => {
            try {
                const response = await fetch('/auth/logged');
                const data = await response.json();
                setUser(data.user[0]);
                // console.log(user);
            } catch (error) {
                console.error(error);
            }
        }
        fetchData();
    }, [user]);
    

    
    return(
        <Box>
            <Nav/>
            <Box width={"100%"} height={"90vh"} display={"flex"} justifyContent={"center"} alignContent={"center"}>
                <Box margin={"auto"}>
                    <Heading color={"#50B2C0"} size={"lg"}>
                        EMAIL
                    </Heading>
                    <Heading size={"md"} mb={"1rem"}>
                        {user.email}
                    </Heading>

                    <Heading color={"#50B2C0"} size={"lg"}>
                        NAME
                    </Heading>
                    <Heading size={"md"} mb={"1rem"}>
                        {user.name}
                    </Heading>

                    <Heading color={"#50B2C0"} size={"lg"}>
                        ADDRESS
                    </Heading>
                    <Heading size={"md"} mb={"1rem"}>
                        {user.address}
                    </Heading>

                    <Heading color={"#50B2C0"} size={"lg"}>
                        AGE
                    </Heading>
                    <Heading size={"md"} mb={"1rem"}>
                        {user.age}
                    </Heading>

                    <Heading color={"#50B2C0"} size={"lg"}>
                        PHONE
                    </Heading>
                    <Heading size={"md"}>
                        {user.phone}
                    </Heading>
                </Box>
            </Box>
        </Box>
    );
}


export default UserProfile;