import React, { useEffect, useState } from "react";
import { Box, Heading } from "@chakra-ui/react";
import CartItem from "./CartItem";
import Nav from "./Nav";

const Cart = () => {

    const [items, setItems] = useState([]);

    useEffect( () => {
        fetch('/auth/logged')
            .then(loginInfo => loginInfo.json())
            .then(login => {
                if(login.login == 1) {
                    fetch(`/api/cart/${login.username}/products`)
                        .then(response => response.json())
                        .then(data => {
                            setItems(data);
                        })
                        .catch(error => console.error(error))
                }
            })
    }, [items]);

    return(
        <Box>
            <Nav/>
            <Box display={"flex"} 
                width={"100%"} 
                justifyContent={"start"} 
                alignItems={"center"}
                gap={"1rem"}
                flexWrap={"wrap"}
                maxWidth={"1350px"}
                m={"auto"}
                pt={"1rem"}>
                {items.map(b => 
                    <CartItem   key={items.findIndex(i => i == b)} 
                                id={b.id} 
                                title={b.title} 
                                price={b.price}
                                img={b.img}
                                timestamp={b.timestamp}>
                    </CartItem>)}
            </Box>
        </Box>
    )
}

export default Cart;